html {
  font-size: 62.5%;
}
@media (max-width: 992px) {
  html {
    font-size: 50%;
  }
}
@media (max-width: 600px) {
  html {
    font-size: 37.5%;
  }
}

@media (min-width: 1920px) {
  html {
    font-size: 75%;
  }
}
@media (min-width: 2500px) {
  html {
    font-size: 100%;
  }
}
@media (min-width: 3500px) {
  html {
    font-size: 150%;
  }
}
